
/* Variabler */
@import url("https://fonts.googleapis.com/css2?family=Inter:wght@300;400;500;600;700;800&display=swap");

:root {
  /* Shadows */
  --primary-box-shadow:0px 8px 18px 0px rgba(117, 141, 166, 0.15);

  /* gaps and padding. Bruker på grid mellom to kolonner og som høyde for hvert element. Ikke endre på disse. Lag heller nye. */
  --m-gap-small:30px;
  --m-gap-medium:60px;
  --m-gap-large:90px;
  --m-gap-xl:120px;

  /* margin */
  --m-margin-small:0px 0px 5px 0px;
  --m-margin-medium:0px 0px 15px 0px;
  --m-margin-large:0px 0px 44px 0px;

  /* Border Radius */
  --m-border-radius-img:8px;

  /* Standard-colors */
  --m-color-transparent:transparent;
  --m-color-white:#ffffff;
  --m-color-grey:#f8f8fb;
  --m-color-dark:#3D3D3C;
  --m-color-black:#000000;
  --m-color-white-opacity:rgba(255, 255, 255, 0.9);
  --m-color-grey-border:#E5E5E5;

  /* 1310 Brand */
  --m-color-trettenti:#0D2D66;

  /* Moava Brand */
  --m-color1:#72CF39;
  --m-color1-hover:#67BA33;
  --m-color1-light:#EAF8E2;
  --m-color1-dark:#136E3C;
  --m-color2:#4AA0D5;
  --m-color2-hover:#4390C0;
  --m-color2-light:#E4F1F9;
  --m-color2-dark:#31698C;
}

/* Font Weights */
.fw1 { font-weight: 100; }
.fw2 { font-weight: 200; }
.fw3 { font-weight: 300; }
.fw4 { font-weight: 400; }
.fw5 { font-weight: 500; }
.fw6 { font-weight: 600; }
.fw7 { font-weight: 700; }
.fw8 { font-weight: 800; }
.fw9 { font-weight: 900; }

/* Font Sizes */
/* Headers*/
.f1 { font-size: 3.5rem; } /* 56px */;
.f2 { font-size: 3rem; } /* 48px */;
.f3 { font-size: 2.25rem; } /* 36px */;
/* Normal tekst */
.f4 { font-size: 1.5rem; } /* 24px */;
.f5 { font-size: 1.25rem; } /* 20px */;
.f6 { font-size: 1rem; } /* Standard tekst font - 16px */;
.f7 { font-size: .875rem; } /* 14px */;
.f8 { font-size: .75rem; } /* Veldig liten */

/* Text Alignments */
.tl { text-align: left; }
.tc { text-align: center; }
.tr { text-align: right; }

